<template>
  <Wrapper :params="params" :border="false">
    <a-form-item
      :label="params.options.label"
      :label-col="{ span: params.options.labelSpan }"
      :wrapper-col="{ span: params.options.wrapperSpan }"
      :labelAlign="params.options.labelAlign"
      :rules="rules"
    >
      <a-cascader
        :value="defaultValue"
        :options="options"
        :show-search="{ filter }"
        :size="params.options.size"
        :placeholder="params.options.placeholder"
        :disabled="params.options.disabled"
        style="width: 100%;"
      />
    </a-form-item>
  </Wrapper>
</template>

<script setup>
import { computed } from 'vue';
import Wrapper from '../wrapper'
import { defineProps } from 'vue';
import { isJsonString } from '@/utils/util';

const props = defineProps({ 
  params: Object,
})

const rules = computed(() => {
  return [
    {
      required: props.params.options.required,
      message: props.params.options.requiredHint,
      trigger: 'change'
    },
  ]
})

const defaultValue = computed(() => {
  return props.params.options.defaultValue
})

const options = computed(() => {
  const data = props.params.options.data
  if (isJsonString(data)) {
    return JSON.parse(data)
  } else {
    return []
  }
})

const filter = (inputValue, path) => {
  return path.some(option => option.label.toLowerCase().indexOf(inputValue.toLowerCase()) > -1);
};

</script>