<template>
  <a-divider>常规属性</a-divider>
  <a-form
    :model="element"
    :label-col="{ span: 10 }"
    :wrapper-col="{ span: 14 }"
    autocomplete="off"
    labelAlign="left"
  >
    <a-form-item label="显示类型" name="type" >
      <a-select v-model:value="element.options.type" size="small">
        <a-select-option value="default">default</a-select-option>
        <a-select-option value="primary">primary</a-select-option>
        <a-select-option value="dashed">dashed</a-select-option>
        <a-select-option value="link">link</a-select-option>
        <a-select-option value="text">text</a-select-option>
      </a-select>
    </a-form-item>
    <a-form-item label="danger" name="danger" >
      <a-switch v-model:checked="element.options.danger" />
    </a-form-item>
    <a-form-item label="背景透明" name="ghost" >
      <a-switch v-model:checked="element.options.ghost" />
    </a-form-item>
    <a-form-item label="位置偏移" name="labelSpan" >
      <a-input-number class="input" v-model:value="element.options.offset" :min="0" :max="24" size="small" />
    </a-form-item>
    <a-form-item label="隐藏" name="hidden" >
      <a-switch v-model:checked="element.options.hidden" />
    </a-form-item>
    <a-form-item label="禁用" name="disabled" >
      <a-switch v-model:checked="element.options.disabled" />
    </a-form-item>
  </a-form>
  <a-divider>事件属性</a-divider>
  <div class="event_list">
    <div class="event_item" v-for="(name, index) in eventNameList" :key="index">
      <div class="title">{{name}}</div>
      <a-button type="default" shape="round" size="small" @click="() => openEditor(name)">
        <a-icon name="EditOutlined" />
        编辑事件
      </a-button>
    </div>
  </div>
  <codeEditor v-model="element.options[eventName]" v-model:open="open" :title="title" :message-alert="message" />
</template>

<script setup>
import { computed, inject, reactive, ref } from 'vue'
import codeEditor from '@/components/code-editor/code-editor.vue';

const WidgetData = inject('$WidgetData')
const element = reactive(WidgetData.find(WidgetData.selectKey))

let eventName = ref('')
const open = ref(false)
const eventNameList = ['onClick']

const openEditor = (name) => {
  eventName.value = name 
  open.value = true
}

const title = computed(() => {
  return element.key + '.' + eventName.value
})

const message = computed(() => {
  return eventName.value
})

</script>

<style lang="less" scoped>
.event_list{
  .event_item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    margin-bottom: 16px;
    .title{
      font-size: 13px;
      overflow: hidden;
      white-space: nowrap;
    }
  }
}
</style>