<template>
  <Wrapper :params="params" :border="false">
    <a-form-item
      :label="params.options.label"
      :label-col="{ span: params.options.labelSpan }"
      :wrapper-col="{ span: params.options.wrapperSpan }"
      :labelAlign="params.options.labelAlign"
    >
      <a-slider :value="params.options.defaultValue" :range="params.options.range" :disabled="params.options.disabled" :marks="marks" />
    </a-form-item>
  </Wrapper> 
</template>

<script setup>
import Wrapper from '../wrapper'
import { computed, defineProps } from 'vue';

const props = defineProps({ 
  params: Object,
})

const marks = computed(() => {
  let marks = {}
  props.params.options.marksArray.forEach(element => {
    if (element.key !== '') {
      marks[element.key] = element['label']
    }
  });
  return marks
})

</script>